@import url(https://fonts.googleapis.com/css2?family=Mrs+Saint+Delafield&family=Poppins:wght@400;700&display=swap);
aside {
  background-color: #362A2E;
  color: white;
  width: 25%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: fixed;
  height: 100vh;
  font-size: 11px;
}

.aside-title, .aside-info, .signature {
  margin-left: 17%;
  background-color: #362A2E;
}

.aside-title {
  color: #F37B83;
}

.aside-info {
  color: #FAEFFF;
  margin-bottom: 20px;
}

.signature {
  font-family: 'Mrs Saint Delafield', cursive;
  margin-top: 20%;
  color: #A88590;
  font-size: 25px;
}

@media screen and (max-width: 900px) {
  .display {
    display: none;
  }
}
.head, .head-p, a, .head-contact, .head-main, .head-title, .head-subTitle, .head-info {
  background-color: #F9F0FF;
}

.head {
  display: flex;
  justify-content: center;
  align-items:center;
  height: 85vh;
}

.head-main {
  width: 100vw;
  height: 100vh;
}

.head-info {
  padding-top: 10%;
  margin-left:  25%;
  height: 65vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.head-pic {
  width: 280px;
  margin-left: 5%;
}

.head-subTitle {
  color: #492D31;
  font-size: 1.5em;
}

.head-title {
  margin: 25px 0 20px 0;
  font-size: 4em;
  color: #F37B83;
  width: 100%;
}

.head-contact {
  width: 40%;
  margin-left: 28%;
  display: flex;
  justify-content: space-around;
  color: #492D31;
  font-size: 9px;
}

.head-p {
  margin-right: 8%;
}

@media screen and (max-width: 900px) {
  .head {
    display: flex;
    flex-direction: column;
    width: 100vw;
  }

  .head-main {
    height: 100vh;
  }
  
  .head-info {
    margin:  5% 0 2% 5%;
  }
  
  .head-pic {
    margin-left: -3%;
    width: 230px;
  }
  
  .head-subTitle {
    font-size: 1.3em;
    margin-top: 18%;
  }
  
  .head-title {
    font-size: 3em;
    margin-top: 5%;
    width: 79vw;
  }
  
  .head-contact {
    width: 90vw;
    /* height: 100vh; */
    margin: 19% 0 0 5%;
  }
}

.about-main {
  margin: 0 0 10% 28%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about-img {
  width: 200px;
  margin: 10% 0 3% 0;
  border-radius: 50%;
}

.about-p {
  background-color: #362A2E;
  padding: 2% 3% 1% 3%;
  border-radius: 8px;
  width: 50%;
}

@media screen and (max-width: 900px) {
  .about-main {
    margin-left: -3%;
  }
  
  .about-img {
    /* width: 200px; */
    margin-left: 6%;
    /* border-radius: 50%; */
  }
  
  .about-p {
    width: 75%;
    line-height: 28px;
  }
}

.project-main {
  margin: 0 0 10% 28%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.img-width {
  width: 200px;
}

.project-img-div {
  margin-top: 3%;
  width: 85%;
  display: flex;
  justify-content: space-around;
}

.project-img {
  z-index: 1;
  position: relative;
}

.description {
  display: none;
}

.project-img:hover .description{
  display: block;
  background-color: #F37B83;
  color: #FAEFFF;
  position: absolute;
  top: 0px;
  width: 180px;
  height: 145px;
  z-index: 2;
  border-radius: 2px;
  padding: 5%;
}

.description-title {
  font-weight: 700;
  font-size: 15px;
  background-color: #F37B83;
  margin-left: 5%;
}

.description-p {
  background-color: #F37B83;
  color: #FAEFFF;
  font-size: 12px;
  line-height: 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 5%;
}

.tarot-icon {
  background-color: #F37B83;
  width: 35px;
  margin-left: 20%;
}

.trivia-icon {
  background-color: #F37B83;
  width: 18px;
  margin-left: 50%;
}

.hacka-icon {
  background-color: #F37B83;
  width: 20px;
  margin-left: 30%;
}

@media screen and (max-width: 900px) {
  .img-width{
    width: 250px;
    margin-bottom: 7%;
  }

  .project-main {
    margin-left: 0%;
  }
  
  .project-img-div {
    margin-top: 3%;
    width: 85%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .project-img:hover .description{
    width: 225px;
    height: 180px;
  }
  
  .description-title {
    font-size: 20px;
  }
  
  .description-p {
    font-size: 15px;
  }
}

.skills-main {
  margin: 0 0 4% 27%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skill-list {
  color: #F37B83;
  width: 100%;
  background-color: #362A2E;
  margin: 10%;
  padding: 5% 15% 5% 15%;
  font-size: 15px;
}

.skills-div {
  width: 50%;
  display: flex;
  justify-content: space-around;
  margin: 0 0 6% -8%;
}

@media screen and (max-width: 900px) {
  .skills-main {
    margin: 0 0 4% 3%;
  }
  
  .skill-list {
    width: 35vw;
    padding: 5%;
    font-size: 14px;
  }
  
  .skills-div {
    width: 95%;
  }
}
.contact-main {
  margin-left: 28%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-div {
  display: flex;
  margin-top: 5%;
  width: 30vw;
}

.contact-btn {
  color: #FAEFFF;
  width: 25vw;
  display: flex;
  justify-content: center;
  padding: 4%;
  background-color: #F37B83;
  margin: 7%;
}

.contact-p {
  width: 50%;
}

.contact-a {
  background-color: #F37B83;
  color:#FAEFFF;
  font-size: 15px;
}

@media screen and (max-width: 900px) {
  .contact-main {
    margin-left: 0%;
  }
  
  .contact-div {
    display: flex;
    margin-top: 5%;
    width: 70vw;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .contact-btn {
    width: 30vw;
  }
  
  .contact-p {
    width: 80%;
  }
}
footer {
  margin: 8% 0 0 24%;
  padding-top: 3% ;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #362A2E;
}

.copyright {
  font-size: 13px;
  background-color: #362A2E;
  margin: 0 0 4% 4%;
}

@media screen and (max-width: 900px) {
  footer {
    margin-left: 0%;
    padding-top: 0% ;
  }
  
  .copyright {
    font-size: 11px;
    width: 80vw;
    margin: 8% 0 8% 0;
  }
}

* {
  margin: 0;
  padding: 0;
  background-color: #2B2728;
}

body {
  font-family: 'Poppins', sans-serif;
  width: 100vw;
}

p {
  color: #A88590;
  width: 340px;
  font-size: 15px;
  text-align: justify;
  line-height: 27px;
  text-justify: inter-word;
}

h2 {
  margin: 25px 0 25px 0;
  color: #F37B83;
  font-size: 2em;
}

a {
  text-decoration: none;
  color: #492D31;
  font-size: 10px;
}

